'use client';

import { useState } from 'react';
import { Key, Link as LinkIcon, Check, X, Save, Eye, EyeOff } from 'lucide-react';

export default function SettingsPage() {
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState('ai-models');
  
  // API Keys state
  const [showKeys, setShowKeys] = useState<Record<string, boolean>>({});
  const [openaiKey, setOpenaiKey] = useState('');
  const [anthropicKey, setAnthropicKey] = useState('');
  const [googleKey, setGoogleKey] = useState('');
  
  // Default model
  const [defaultModel, setDefaultModel] = useState('anthropic/claude-sonnet-4-5');
  
  // Demo mode
  const [demoMode, setDemoMode] = useState(true);

  const toggleKeyVisibility = (key: string) => {
    setShowKeys(prev => ({ ...prev, [key]: !prev[key] }));
  };

  const handleSaveAPIKeys = async () => {
    setLoading(true);
    setError(null);
    setSuccess(null);

    try {
      // In production, save to secure storage
      localStorage.setItem('openai_key', openaiKey);
      localStorage.setItem('anthropic_key', anthropicKey);
      localStorage.setItem('google_key', googleKey);
      localStorage.setItem('default_model', defaultModel);
      localStorage.setItem('demo_mode', String(demoMode));
      
      setSuccess('Settings saved successfully!');
    } catch (err: any) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  const tabs = [
    { id: 'ai-models', label: 'AI Models' },
    { id: 'ad-platforms', label: 'Ad Platforms' },
    { id: 'preferences', label: 'Preferences' },
  ];

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold">Settings</h1>
        <p className="text-gray-400 mt-1">Configure your AI models and preferences</p>
      </div>

      {success && (
        <div className="bg-green-500/20 border border-green-500/30 text-green-400 px-4 py-3 rounded-lg flex items-center gap-2">
          <Check className="w-5 h-5" />
          {success}
        </div>
      )}

      {error && (
        <div className="bg-red-500/20 border border-red-500/30 text-red-400 px-4 py-3 rounded-lg flex items-center gap-2">
          <X className="w-5 h-5" />
          {error}
        </div>
      )}

      {/* Tabs */}
      <div className="flex gap-2 border-b border-white/10 pb-2">
        {tabs.map(tab => (
          <button
            key={tab.id}
            onClick={() => setActiveTab(tab.id)}
            className={`px-4 py-2 rounded-lg transition-colors ${
              activeTab === tab.id
                ? 'bg-purple-500/20 text-purple-300 border border-purple-500/30'
                : 'text-gray-400 hover:bg-white/5'
            }`}
          >
            {tab.label}
          </button>
        ))}
      </div>

      {/* AI Models Tab */}
      {activeTab === 'ai-models' && (
        <div className="space-y-6">
          {/* API Keys Card */}
          <div className="p-6 rounded-xl bg-white/5 border border-white/10">
            <div className="flex items-center gap-2 mb-4">
              <Key className="w-5 h-5 text-purple-400" />
              <h3 className="text-lg font-semibold">API Keys</h3>
            </div>
            <p className="text-sm text-gray-400 mb-6">
              Configure your AI API keys for model access. Keys are stored securely.
            </p>

            <div className="space-y-4">
              {/* OpenAI Key */}
              <div>
                <label className="block text-sm font-medium mb-2">OpenAI API Key</label>
                <div className="relative">
                  <input
                    type={showKeys.openai ? 'text' : 'password'}
                    placeholder="sk-proj-..."
                    value={openaiKey}
                    onChange={(e) => setOpenaiKey(e.target.value)}
                    className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 focus:border-purple-500/50 focus:outline-none"
                  />
                  <button
                    type="button"
                    onClick={() => toggleKeyVisibility('openai')}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-white"
                  >
                    {showKeys.openai ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                  </button>
                </div>
                <p className="text-xs text-gray-500 mt-1">
                  For GPT-5, GPT-5.1 models. Get from{' '}
                  <a href="https://platform.openai.com/api-keys" target="_blank" className="text-purple-400 hover:underline">
                    OpenAI Platform
                  </a>
                </p>
              </div>

              {/* Anthropic Key */}
              <div>
                <label className="block text-sm font-medium mb-2">Anthropic API Key</label>
                <div className="relative">
                  <input
                    type={showKeys.anthropic ? 'text' : 'password'}
                    placeholder="sk-ant-api03-..."
                    value={anthropicKey}
                    onChange={(e) => setAnthropicKey(e.target.value)}
                    className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 focus:border-purple-500/50 focus:outline-none"
                  />
                  <button
                    type="button"
                    onClick={() => toggleKeyVisibility('anthropic')}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-white"
                  >
                    {showKeys.anthropic ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                  </button>
                </div>
                <p className="text-xs text-gray-500 mt-1">
                  For Claude Sonnet/Opus/Haiku 4.5. Get from{' '}
                  <a href="https://console.anthropic.com/" target="_blank" className="text-purple-400 hover:underline">
                    Anthropic Console
                  </a>
                </p>
              </div>

              {/* Google Key */}
              <div>
                <label className="block text-sm font-medium mb-2">Google AI Studio API Key</label>
                <div className="relative">
                  <input
                    type={showKeys.google ? 'text' : 'password'}
                    placeholder="AIza..."
                    value={googleKey}
                    onChange={(e) => setGoogleKey(e.target.value)}
                    className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 focus:border-purple-500/50 focus:outline-none"
                  />
                  <button
                    type="button"
                    onClick={() => toggleKeyVisibility('google')}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-white"
                  >
                    {showKeys.google ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                  </button>
                </div>
                <p className="text-xs text-gray-500 mt-1">
                  For Gemini 3 models. Get from{' '}
                  <a href="https://aistudio.google.com/apikey" target="_blank" className="text-purple-400 hover:underline">
                    Google AI Studio
                  </a>
                </p>
              </div>
            </div>
          </div>

          {/* Default Model Card */}
          <div className="p-6 rounded-xl bg-white/5 border border-white/10">
            <h3 className="text-lg font-semibold mb-4">Default AI Model</h3>
            <p className="text-sm text-gray-400 mb-4">
              Select the default model for AI chat. You can change this per-conversation.
            </p>
            
            <select
              value={defaultModel}
              onChange={(e) => setDefaultModel(e.target.value)}
              className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 focus:border-purple-500/50 focus:outline-none"
            >
              <optgroup label="OpenAI GPT-5">
                <option value="openai/gpt-5">GPT-5 (Flagship)</option>
                <option value="openai/gpt-5-mini">GPT-5 Mini</option>
                <option value="openai/gpt-5-nano">GPT-5 Nano</option>
              </optgroup>
              <optgroup label="OpenAI GPT-5.1">
                <option value="openai/gpt-5.1">GPT-5.1 Thinking</option>
                <option value="openai/gpt-5.1-chat-latest">GPT-5.1 Instant</option>
              </optgroup>
              <optgroup label="Anthropic Claude 4.5">
                <option value="anthropic/claude-sonnet-4-5">Claude Sonnet 4.5 (Recommended)</option>
                <option value="anthropic/claude-opus-4-5">Claude Opus 4.5</option>
                <option value="anthropic/claude-haiku-4-5">Claude Haiku 4.5</option>
              </optgroup>
              <optgroup label="Google Gemini 3">
                <option value="google/gemini-3">Gemini 3</option>
                <option value="google/gemini-3-pro">Gemini 3 Pro</option>
                <option value="google/gemini-3-flash">Gemini 3 Flash</option>
              </optgroup>
            </select>
          </div>

          <button
            onClick={handleSaveAPIKeys}
            disabled={loading}
            className="flex items-center gap-2 px-6 py-3 rounded-lg bg-purple-600 hover:bg-purple-500 transition-colors disabled:opacity-50"
          >
            <Save className="w-5 h-5" />
            {loading ? 'Saving...' : 'Save API Settings'}
          </button>
        </div>
      )}

      {/* Ad Platforms Tab */}
      {activeTab === 'ad-platforms' && (
        <div className="space-y-6">
          <div className="p-6 rounded-xl bg-white/5 border border-white/10">
            <div className="flex items-center gap-2 mb-4">
              <LinkIcon className="w-5 h-5 text-blue-400" />
              <h3 className="text-lg font-semibold">Connected Ad Platforms</h3>
            </div>
            <p className="text-sm text-gray-400 mb-6">
              Manage your advertising platform connections.
            </p>

            <div className="space-y-4">
              {['Google Ads', 'Meta Ads', 'LinkedIn Ads'].map((platform) => (
                <div key={platform} className="flex items-center justify-between p-4 rounded-lg bg-white/5 border border-white/10">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 rounded-lg bg-emerald-500/20 flex items-center justify-center">
                      <Check className="w-5 h-5 text-emerald-400" />
                    </div>
                    <div>
                      <p className="font-medium">{platform}</p>
                      <p className="text-sm text-gray-400">Connected</p>
                    </div>
                  </div>
                  <button className="px-4 py-2 rounded-lg bg-red-500/20 text-red-400 hover:bg-red-500/30 transition-colors">
                    Disconnect
                  </button>
                </div>
              ))}
            </div>
          </div>
        </div>
      )}

      {/* Preferences Tab */}
      {activeTab === 'preferences' && (
        <div className="space-y-6">
          <div className="p-6 rounded-xl bg-white/5 border border-white/10">
            <h3 className="text-lg font-semibold mb-4">Display Preferences</h3>
            
            <div className="space-y-4">
              <div className="flex items-center justify-between p-4 rounded-lg bg-white/5">
                <div>
                  <p className="font-medium">Demo Mode</p>
                  <p className="text-sm text-gray-400">Show sample data when no real data is available</p>
                </div>
                <button
                  onClick={() => setDemoMode(!demoMode)}
                  className={`w-12 h-6 rounded-full transition-colors ${
                    demoMode ? 'bg-purple-600' : 'bg-gray-600'
                  }`}
                >
                  <div className={`w-5 h-5 rounded-full bg-white transition-transform ${
                    demoMode ? 'translate-x-6' : 'translate-x-0.5'
                  }`} />
                </button>
              </div>
            </div>
          </div>

          <button
            onClick={handleSaveAPIKeys}
            disabled={loading}
            className="flex items-center gap-2 px-6 py-3 rounded-lg bg-purple-600 hover:bg-purple-500 transition-colors disabled:opacity-50"
          >
            <Save className="w-5 h-5" />
            {loading ? 'Saving...' : 'Save Preferences'}
          </button>
        </div>
      )}
    </div>
  );
}

